<template>
  <div class="schedule">
  	<!-- <h3>中超-助攻榜</h3> -->
  	<dl>
  		<dt>
  		  <div class="type3">比赛时间</div>
  			<div class="type1">主队</div>
  			<div class="type1">客队</div>
  			<!-- <div class="type3">助攻数</div> -->
  		</dt>
  		<dd v-for="(item, index) in schedule">
  			<div class="type3" v-text="item.start_play"></div>
  			<div class="type1">
  				<img :src="'//img.dongqiudi.com/data/pic/'+item.team_A_id+'.png'"/>
  				<span v-html="item.team_A_name"></span>
  			</div>
  			<div class="type1">
  				<img :src="'//img.dongqiudi.com/data/pic/'+item.team_B_id+'.png'"/>
  				<span v-html="item.team_B_name"></span>
  			</div>
  			<!-- <div class="type3" v-text="item.count"></div> -->
  		</dd>
  	</dl>
  </div>
</template>

<script>
  export default {
  	props: {
  	  schedule: {
  		type: Array,
  		required: true
  	  }
  	},
  	mounted() {
      setTimeout(() => {
      	this.$emit('updateScroll')
      },20)
  	}
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.schedule
  h3
  	text-align: left
  p
  	text-align: left
  dl
  	background: #e2e2e2
  	font-siez: 1rem
	dt
	  display: flex
	  padding: .6rem 0
	  .type1
	  	flex: 4
	  	text-align: center
	  .type3
	  	flex: 2
	dd
	  display: flex
	  border-bottom: 1px solid #ddd
	  padding: 1rem 0
	  font-size: 12px
	  position: relative
	  .type1
	  	flex: 4
	  	text-align: center
	  	img
	  	  width: 20px
	  	  position: absolute
	  	  top: .8rem
	  	  left: 3rem
	  .type3
	  	flex: 2
</style>


